
@charset "UTF-8";
/* 清除内外边距 */
body, h1, h2, h3, h4, h5, h6, hr, p, blockquote,
dl, dt, dd, ul, ol, li,
pre,
fieldset, lengend, button, input, textarea,
th, td {
  /* table elements 表格元素 */
  margin: 0;
  padding: 0;
}

/* 设置默认字体 */
body,
button, input, select, textarea {
  /* for ie */
  /*font: 12px/1 Tahoma, Helvetica, Arial, "宋体", sans-serif;*/
  font: 12px/1 Tahoma, Helvetica, Arial, "\5b8b\4f53", sans-serif;
  /* 用 ascii 字符表示，使得在任何编码下都无问题 */
}
h1 {
  font-size: 18px;
  /* 18px / 12px = 1.5 */
}
h2 {
  font-size: 16px;
}
h3 {
  font-size: 14px;
}
h4, h5, h6 {
  font-size: 100%;
}
address, cite, dfn, em, var {
  font-style: normal;
}

/* 将斜体扶正 */
code, kbd, pre, samp, tt {
  font-family: "Courier New", Courier, monospace;
}

/* 统一等宽字体 */
small {
  font-size: 12px;
}

/* 小于 12px 的中文很难阅读，让 small 正常化 */
/* 重置列表元素 */
ul, ol {
  list-style: none;
}

/* 重置文本格式元素 */
a {
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}
abbr[title], acronym[title] {
  /* 注：1.ie6 不支持 abbr; 2.这里用了属性选择符，ie6 下无效果 */
  border-bottom: 1px dotted;
  cursor: help;
}
q:before, q:after {
  content: '';
}

/* 重置表单元素 */
legend {
  color: #000;
}

/* for ie6 */
fieldset, img {
  border: none;
}

/* img 搭车：让链接里的 img 无边框 */
/* 注：optgroup 无法扶正 */
button, input, select, textarea {
  font-size: 100%;
  /* 使得表单元素在 ie 下能继承字体大小 */
}

/* 重置表格元素 */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* 重置 hr */
hr {
  border: none;
  height: 1px;
}

/* 让非ie浏览器默认也显示垂直滚动条，防止因滚动条引起的闪烁 */
html {
  overflow-y: scroll;
}
body {
  background-color: #f0f0f0;
}
button {
  cursor: pointer;
  border-radius: 2px;
}
input {
  border-radius: 2px;
}
a:hover {
  text-decoration: none;
}
.control-view {
  margin: 60px 0;
  position: relative;
  overflow: hidden;
}
.control-view .split-border-menu {
    position: absolute;
    left: 0px;
    bottom: 0px;
    width: 160px;
    height: 1px;
    background-color: #DDDDDD;
}
.control-view .split-border {
    position: absolute;
    left: 180px;
    bottom: 0px;
    width: 800px;
    height: 1px;
    background-color: #DDDDDD;
}
.control-view .module-view {
    width: 800px;
    float: left;
    margin-left: 20px;
    background-color: white;
    overflow: hidden;
}
.control-view .module-view .module-body {
      min-height: 810px;
}
.control-view .module-view .module-body .module-title div.title {
        padding-left: 20px;
        font-size: 20px;
        font-weight: bold;
        line-height: 60px;
}
.control-view .module-view .module-body .module-title div.msg {
        padding-left: 20px;
        padding-bottom: 20px;
}
.control-view .module-view .module-body .module-title div.msg p {
          width: 700px;
          color: #858585;
          font-size: 12px;
          line-height: 18px;
}
.main-view {
  width: 980px;
  margin: 0 auto;
}
.main-view .panel {
    min-height: 880px;
    margin-bottom: 100px;
    box-sizing: border-box;
    border: 1px solid #DDDDDD;
    border-radius: 2px;
    margin-bottom: -10000px;
    padding-bottom: 10000px;
}
.list-chuck {
  position: relative;
  padding: 0 20px;
  overflow: hidden;
}
.list-chuck .item {
    float: left;
    font-size: 14px;
    color: #858585;
    box-sizing: border-box;
    /*border-left:1px solid rgb(0,0,0);*/
}
.list-chuck .item-col-1 {
    width: 296px;
}
.list-chuck .item-col-2 {
    width: 100px;
}
.list-chuck .item-col-3 {
    width: 120px;
}
.list-chuck .item-col-4 {
    width: 100px;
}
.list-chuck .item-col-5 {
    width: 140px;
}
.list-chuck .item-two-col {
    position: relative;
    overflow: hidden;
}
.list-chuck .item-two-col .col-item {
      float: left;
      width: 50%;
      text-align: left;
}
.list-chuck .item-two-col .col-item:nth-child(1) {
        text-align: right;
}
.list-header {
  height: 50px;
  line-height: 50px;
  background-color: #F8F8F8;
  font-weight: 500;
}
.list-header .item {
    text-align: center;
}
.list-header .item.item-col-1 {
      text-align: left;
}
.list-body .item {
  text-align: center;
}
.list-body .no-data {
  line-height: 120px;
  font-size: 20px;
  color: #858585;
  text-align: center;
}



