
.control-view {
  margin: 60px 0;
  position: relative;
  overflow: hidden;
}
.control-view .split-border-menu {
    position: absolute;
    left: 0px;
    bottom: 0px;
    width: 160px;
    height: 1px;
    background-color: #DDDDDD;
}
.control-view .split-border {
    position: absolute;
    left: 180px;
    bottom: 0px;
    width: 800px;
    height: 1px;
    background-color: #DDDDDD;
}
.control-view .module-view {
    width: 800px;
    float: left;
    margin-left: 20px;
    background-color: white;
    overflow: hidden;
}
.control-view .module-view .module-body {
      min-height: 810px;
}
.control-view .module-view .module-body .module-title div.title {
        padding-left: 20px;
        font-size: 20px;
        font-weight: bold;
        line-height: 60px;
}
.control-view .module-view .module-body .module-title div.msg {
        padding-left: 20px;
        padding-bottom: 20px;
}
.control-view .module-view .module-body .module-title div.msg p {
          width: 700px;
          color: #858585;
          font-size: 12px;
          line-height: 18px;
}



